      margin: 0 12px 0;
      -webkit-animation-name: cursomaquillaje;
              animation-name: cursomaquillaje;
      -webkit-animation-duration: 3s;
              animation-duration: 3s;
  
  }
  
  .banner__ {
    display: flex;
    justify-content: space-around;
    align-items:center;
  
  }
  
  .banner__img {
  
    -webkit-animation: 3s rotate linear;
    animation: 3s rotate linear ;
    -webkit-transform-origin: 50% 50%;
    transform-origin: 50% 50%;
  
    border-radius: 50% ;
    box-shadow: 0px 0px 15px 15px #e5a97e;
    -webkit-box-shadow: 0px 0px 15px 15px #b26c3a;
    width: 200px;
    height: 300px;
  }
  /* #e5a97e;#b26c3a; #d7b3c4 #762859 para el hover #ec731e;fb32d6 */
  
  
  .banner__img:hover{
    border-radius:50%;
    -webkit-border-radius:50%;
    box-shadow: 0px 0px 15px 15px #ec731e;
    -webkit-box-shadow: 0px 0px 15px 15px #ec731e;
    transform: rotate(360deg);
    -webkit-transform: rotate(360deg);
    }
  
    .right {
  
      -webkit-animation: 3s rotate linear;
      animation: 3s rotate linear ;
      -webkit-transform-origin: 50% 50%;
      transform-origin: 50% 50%;
      display: flex;
      flex-flow:  row wrap;
      align-items:flex-end;
      justify-content: flex-end;
      margin: 10px 250px 800px 10px;
      border-radius: 50% ;
      box-shadow: 0px 0px 15px 15px #b26c3a;
      -webkit-box-shadow: 0px 0px 15px 15px #e5a97e;
      width: 200px;
      height: 300px;
  
    }
  
  
  
  @-webkit-keyframes cursomaquillaje {
      from {
  
          margin-top: 100%;
          width: 300%
          
        }
      
        to {
          margin-top: 0%;
          width: 100%;
          
        }
      }
  
  
  
  @keyframes cursomaquillaje {
      from {
  
          margin-top: 100%;
          width: 300%
          
        }
      
        to {
          margin-top: 0%;
          width: 100%;
          
        }
      }
  
      /* rotando imagenes */
  
      @keyframes rotate {from {-webkit-transform: rotate(0deg);transform: rotate(0deg);}
      to {-webkit-transform: rotate(360deg);transform: rotate(360deg);}}
  @-webkit-keyframes rotate {from {-webkit-transform: rotate(0deg);}
